/**
 * 0x1 Standard App
 * A complete TypeScript application with component system and routing
 */

// Import components and pages
import { Footer } from './components/Footer';
import { Header } from './components/Header';
import { Router } from './lib/router';
import { About } from './pages/about';
import { Home } from './pages/home';
import { NotFound } from './pages/not-found';

// Theme management constants
const STORAGE_KEY = '0x1-dark-mode';

// DOM ready function
function ready(callback: () => void): void {
  if (document.readyState !== 'loading') {
    callback();
  } else {
    document.addEventListener('DOMContentLoaded', callback);
  }
}

// Initialize theme based on saved preference or system setting
function initializeTheme() {
  const savedTheme = localStorage.getItem(STORAGE_KEY);
  const prefersDark = window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches;
  
  // Apply theme based on saved preference or system preference
  if (savedTheme === 'dark' || (savedTheme === null && prefersDark)) {
    document.documentElement.classList.add('dark');
  } else if (savedTheme === 'light') {
    document.documentElement.classList.remove('dark');
  }
  
  // Listen for system preference changes
  window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', (e) => {
    // Only update if user hasn't set a preference
    if (!localStorage.getItem(STORAGE_KEY)) {
      if (e.matches) {
        document.documentElement.classList.add('dark');
      } else {
        document.documentElement.classList.remove('dark');
      }
    }
  });
}

// Initialize the app
ready(() => {
  console.log('DOM ready - initializing app');
  
  // Initialize theme
  initializeTheme();
  
  // Set up theme toggle functionality
  const themeToggle = document.getElementById('theme-toggle');
  if (themeToggle) {
    themeToggle.addEventListener('click', () => {
      // Toggle dark mode
      if (document.documentElement.classList.contains('dark')) {
        document.documentElement.classList.remove('dark');
        localStorage.setItem(STORAGE_KEY, 'light');
      } else {
        document.documentElement.classList.add('dark');
        localStorage.setItem(STORAGE_KEY, 'dark');
      }
    });
  }
  
  // Find main content area for router
  const main = document.getElementById('main-content');
  if (!main) {
    console.error('Main content element not found');
    return;
  }
  
  console.log('Using existing HTML structure for app');
  
  // Create router with main content area as the root
  const router = new Router(main);
  
  // Define routes
  router.addRoute('/', Home);
  router.addRoute('/about', About);
  router.setNotFound(NotFound);
  
  // Initialize router
  router.init();
});
